<template>
  <div id="login">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="form">
          <h3 @click="showRegister">创建账户</h3>
          <transition name="slide">
            <div :class="{show: isShowRegister}" class="register">
              <input type="text" v-model="register.username" placeholder="用户名">
              <input type="password" v-model="register.password" @keyup.enter="onRegister" placeholder="密码">
              <p :class="{error: register.isError}"> {{ register.notice }}</p>
              <div class="button" @click="onRegister">创建账号</div>
            </div>
          </transition>
          <h3 @click="showLogin">登录</h3>
          <transition name="slide">
            <div :class="{show: isShowLogin}" class="login">
              <input type="text" v-model="login.username" placeholder="输入用户名">
              <input type="password" v-model="login.password" @keyup.enter="onLogin" placeholder="密码">
              <p :class="{error: login.isError}"> {{ login.notice }}</p>
              <div class="button" @click="onLogin"> 登录</div>
            </div>
          </transition>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'


export default {
  data() {
    return {
      isShowLogin: true,
      isShowRegister: false,
      login: {
        username: '',
        password: '',
        notice: '输入用户名和密码',
        isError: false
      },
      register: {
        username: '',
        password: '',
        notice: '创建账号后，请记住用户名和密码',
        isError: false
      }
    }
  },
  methods: {
    ...mapActions({
        loginUser: 'login',
        registerUser: 'register',
      }
    ),
    showLogin() {
      this.isShowLogin = true
      this.isShowRegister = false
    },
    showRegister() {
      this.isShowLogin = false
      this.isShowRegister = true
    },
    onRegister() {
      let result1 = this.validUsername(this.register.username)
      let result2 = this.validPassword(this.register.password)
      if (!result1.isValid) {
        this.register.isError = true
        this.register.notice = result1.notice
        return
      }
      if (!result2.isValid) {
        this.register.isError = true
        this.register.notice = result2.notice
        return
      }
      this.register.isError = false
      this.register.notice = ''
      console.log(`start register..., username: ${this.register.username} , password: ${this.register.password}`)

      this.registerUser({
        username: this.register.username,
        password: this.register.password
      }).then(() => {
        this.register.isError = false
        this.register.notice = ''
        this.$router.push({path: 'notebooks'})
      }).catch(data => {
        this.register.isError = true
        this.register.notice = data.msg
      })

      /*     request('/auth/register','POST',{
             username:this.register.username,
             password:this.register.password
           })
             .then(data=>{
               console.log(data);
             })*/
    },
    onLogin() {
      let result1 = this.validUsername(this.login.username)
      let result2 = this.validPassword(this.login.password)
      //格式校验
      if (!result1.isValid) {
        this.login.isError = true
        this.login.notice = result1.notice
        return
      }
      if (!result2.isValid) {
        this.login.isError = true
        this.login.notice = result2.notice
        return
      }
      console.log(`start login..., username: ${this.login.username} , password: ${this.login.password}`)
      //登陆校验
      this.loginUser({
        username: this.login.username,
        password: this.login.password
      }).then(() => {
        this.login.isError = false
        this.login.notice = ''
        Bus.$emit('userInfo', {username: this.login.username})//相当于去指挥中心调数据了
        this.$router.push({path: 'notebooks'})
        console.log('跳转');
      }).catch(data => {
        this.login.isError = true
        this.login.notice = data.msg
      })
    },

    validUsername(username) {
      return {
        isValid: /^[\w\u4e00-\u9fa5]{3,15}$/.test(username),
        notice: '用户名3~15个字符，仅限于字母数字下划线中文'
      }
    },
    validPassword(password) {
      return {
        isValid: /^.{6,16}$/.test(password),
        notice: '用户名3~15个字符，仅限于字母数字下划线中文'
      }
    }
  }
}
</script>


<style lang="less">
.modal-mask {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../assets/e5fbbc6162c1ddd4911884814a5dccbe.jpeg) no-repeat center/100%;
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.form {
  color: #24292f;
  width: 400px;
  height: 280px;
  margin: 0 auto;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.5);
  border-left: 1px solid #ccc;
  font-family: Helvetica, Arial, sans-serif;
  overflow: hidden;

  h3 {
    padding: 10px 20px;
    margin-top: -1px;
    font-weight: normal;
    font-size: 16px;
    border-top: 1px solid #eee;
    cursor: pointer;

    &:nth-of-type(2) {
      border-bottom: 1px solid #eee;
    }
  }

  .button {
    background-color: #2bb964;
    height: 36px;
    line-height: 36px;
    text-align: center;
    font-weight: bold;
    color: #fff;
    border-radius: 4px;
    margin-top: 18px;
    cursor: pointer;
  }

  .login, .register {
    padding: 0 20px;
    border-top: 1px solid #eee;
    height: 0;
    overflow: hidden;
    transition: height .4s;

    &.show {
      height: 193px;
    }

    input {
      display: block;
      width: 100%;
      height: 35px;
      line-height: 35px;
      padding: 0 6px;
      border-radius: 4px;
      border: 1px solid #ccc;
      outline: none;
      font-size: 14px;
      margin-top: 10px;
    }

    input:focus {
      border: 3px solid #9dcaf8;
    }

    p {
      font-size: 12px;
      margin-top: 10px;
      color: #444;
    }

    .error {
      color: red;
    }
  }

  .login {
    border-top: 0;
  }
}

</style>



